#new-task-form {
  grid-template-columns: 4fr 2fr 1fr;
  justify-content: center;
  align-items: stretch;
  gap: 0;
  max-width: 650px;
  margin: 0;
  padding: 1rem;
  display: grid;
}

#new-task-form input, #new-task-form button {
  border: 0;
  outline: 0;
  height: 100%;
  padding: .5rem;
  display: block;
}

#new-task-form select {
  border: 0;
  outline: 0;
  height: 100%;
  display: block;
}

#new-task-form input:focus, #new-task-form input:active, #new-task-form select:focus, #new-task-form select:active {
  background-color: #d3d3d3;
}

#new-task-text {
  border-radius: 15px 0 0 15px;
  margin: 0;
}

#new-task-type {
  margin: 0;
}

#new-task-submit {
  cursor: pointer;
  background-color: orange;
  border-radius: 0 15px 15px 0;
  margin: 0;
}

#new-task-submit:active, #new-task-submit:focus, #new-task-submit:hover {
  color: #fff;
  background-color: green;
}

